export default {
	name: 'stepInfo',
	props:{
		option: {
			type: Object
		}
	},
	data () {
		return {
			info: '',
			firstFoo: [],
			secondFoo: [],
			record: [],
			nowperson: '',
			swiperList: [],
			dotStyle: false,
			cardCur: 0,
		}
	},
	mounted() {
		uni.showLoading({
			title:'加载中。。。',
			mask:true
		})
		this.info = this._props.option;
		console.log(this.info)
		this.getFirstInfo();
		this.getSecondInfo();
		this.getRecord();
		this.getNowPeople();
		this.getImage();
		this.$nextTick(function(){
			uni.hideLoading()
		})
	},
	methods: {
		zzy(path) {
			uni.downloadFile({
				url: path,
				success: res => {
					if (res.statusCode === 200) {
						// console.log(res.tempFilePath);
						var tempFilePath = res.tempFilePath;
		
						uni.openDocument({
							filePath: tempFilePath,
							success() {
								uni.hideLoading()
							}
						})
					}
				}
			});
		},
		showMoadel(title) {
			uni.showModal({
				title: '提示',
				content: title,
				showCancel: false,
				success: function(res) {
					if (res.confirm) {}
				}
			});
		},
		download() {
			var self = this;
			uni.showLoading({
				title:'下载中。。。',
				mask:true
			})
			uni.request({
				url: self.$store.state.urlPrefix + '/Web/Download_WordSingle',
				data: {
					no: self.info.No,
				},
			}).then(data => {
				var [error,res] = data;
				console.log(res.data)
				if (res.data.code == 0) {
					var url = res.data.data[0];
					self.zzy(url)
				} else {
					uni.hideLoading();
					self.showMoadel('下载失败！')
				}
			})
		},
		previewImage(url){
			let data = url.split(',');
			for (let i=0;i<data.length;i++) {
				data[i] = data[i].replace(/\\/g,'/');
			}
			console.log(data);
			var that = this;
			uni.previewImage({
				urls: data,
				current: data[0]
			});
		},
		ViewImage(e) {
			uni.previewImage({
				urls: this.swiperList,
				current: e.currentTarget.dataset.url
			});
		},
		cardSwiper(e) {
			this.cardCur = e.detail.current
		},
		getFirstInfo() {
			var self = this;
			uni.request({
				url: self.$store.state.urlPrefix + '/Process/Get_Troubleshooting',
				data: {
					YSCD: self.info.YSCD,
					BranchId: self.info.BranchId
				},
			}).then(data => {
				var [error,res] = data;
				console.log(res.data)
				if (res.data != '[]') {
					self.firstFoo = JSON.parse(res.data);
					self.firstFoo = self.firstFoo[0]
				}
			})
		},
		getSecondInfo() {
			var self = this;
			uni.request({
				url: self.$store.state.urlPrefix + '/Process/Get_review',
				data: {
					No: self.info.No,
					BranchId: self.info.BranchId
				},
			}).then(data => {
				var [error,res] = data;
				
				if (res.data != '暂无数据') {
					self.secondFoo = JSON.parse(res.data);
					self.secondFoo = self.secondFoo[0];
					console.log(self.secondFoo)
					if (self.secondFoo.AQZK == '1') {
						self.secondFoo.AQZK = '轻微隐患'
					} else if (self.secondFoo.AQZK == '2') {
						self.secondFoo.AQZK = '一般隐患'
					}
				}
			})
		},
		getRecord() {
			var self = this;
			uni.request({
				url: self.$store.state.urlPrefix + '/Process/Get_Processing',
				data: {
					No: self.info.No
				},
			}).then(data => {
				var [error,res] = data;
				
				if (res.data != '暂无数据') {
					console.log(JSON.parse(res.data));
					self.record = JSON.parse(res.data)
				}
			})
		},
		
		getNowPeople() {
			var self = this;
			uni.request({
				url: self.$store.state.urlPrefix + '/Process/Get_Executor',
				data: {
					BranchId: self.info.BranchId,
					No: self.info.No,
				},
			}).then(data => {
				var [error,res] = data;
				
				self.nowperson = res.data;
			})
		},
		
		getImage() {
			var self = this;
			uni.request({
				url: self.$store.state.urlPrefix + '/Process/Get_YHimg',
				data: {
					No: self.info.No
				},
			}).then(data => {
				var [error,res] = data;
				let aa = res.data[0].url;
				self.swiperList = aa.split(',');
				console.log(aa)
			})
		}
	},
}
